html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Nunito", Arial, sans-serif;
  line-height: 1.25;
  font-size: 16px;
  color: #505050;
  scroll-behavior: smooth;
}
@media (min-width: 576px) {
  body {
    font-size: 18px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

svg path {
  fill: inherit;
}

a {
  color: inherit;
}
a:hover, a:focus {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

@font-face {
  font-family: "PlayfairDisplay";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("PlayfairDisplay-Bold"), url("../fonts/PlayfairDisplay-Bold.woff2") format("woff2"), url("../fonts/PlayfairDisplay-Bold.woff") format("woff");
}
@font-face {
  font-family: "Roboto-Medium";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: local("Roboto-Medium"), url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
}
@font-face {
  font-family: "Nunito";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: local("NunitoSans-Regular"), url("../fonts/NunitoSans-Regular.woff2") format("woff2"), url("../fonts/NunitoSans-Regular.woff") format("woff");
}
.btn {
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
}

.btn--main {
  display: inline-block;
  width: 100%;
  padding: 10px 20px;
  background-color: #fdc435;
  border-radius: 8px;
  text-decoration: none;
  text-align: center;
  color: #25282b;
  font-family: "Nunito", Arial, sans-serif;
  transition: background ease-in 0.3s;
}
.btn--main:hover, .btn--main:focus {
  background-color: #fdb503;
}

.btn--sm {
  margin: 15px 0;
}
@media (min-width: 768px) {
  .btn--sm {
    width: 200px;
  }
}

.container {
  max-width: 1216px;
  padding: 0 15px;
  margin: 0 auto;
}

.form input,
.form textarea {
  width: 100%;
  padding: 5px 10px;
  margin-bottom: 15px;
  border: 1px solid transparent;
  border-radius: 5px;
  transition: border ease-in 0.3s;
}
.form input::placeholder,
.form textarea::placeholder {
  color: #505050;
}
.form input:focus,
.form textarea:focus {
  outline: none;
  border-color: #505050;
}
.form textarea {
  min-height: 120px;
  resize: none;
}
.form .help {
  margin-top: -20px !important;
  margin-bottom: 20px !important;
}

.section {
  padding-bottom: 20px;
}
@media (min-width: 768px) {
  .section {
    padding-bottom: 60px;
  }
}

.section__text {
  line-height: 22px;
}
@media (min-width: 768px) {
  .section__text {
    margin-bottom: 20px;
    line-height: 30px;
  }
}

.title {
  font-family: "PlayfairDisplay", Georgia, serif;
  font-weight: 400;
}

.title--section {
  position: relative;
}
.title--section::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: -10px;
  display: block;
  width: 100px;
  height: 14px;
  transform: skew(-12deg);
  background: rgba(253, 196, 53, 0.5);
  z-index: -1;
}

.title--first {
  margin: 0 0 20px 0;
  font-size: 30px;
  color: #25282b;
}
@media (min-width: 576px) {
  .title--first {
    font-size: 42px;
  }
}
@media (min-width: 1024px) {
  .title--first {
    margin-bottom: 32px;
    font-size: 60px;
  }
}

.title--second {
  margin: 0 0 0 10px;
  color: #25282b;
  font-size: 28px;
}
@media (min-width: 576px) {
  .title--second {
    font-size: 32px;
  }
}
@media (min-width: 768px) {
  .title--second {
    font-size: 36px;
  }
}

.title-white {
  color: #ffffff;
}

.about__container {
  padding-bottom: 30px;
}
@media (min-width: 1024px) {
  .about__container {
    display: flex;
    padding-bottom: 60px;
  }
}

.about__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 1024px) {
  .about__text {
    width: 60%;
    margin-bottom: 35px;
    margin-right: auto;
  }
}

.about__wrapper-img {
  display: flex;
  justify-content: center;
}

.about__img {
  border-radius: 15px;
}

.about__actions .btn + .btn {
  margin-left: 10px;
}

@media (min-width: 768px) {
  .contacts__col {
    width: 50%;
  }
}

.contacts__form {
  padding: 30px 15px;
}
@media (min-width: 768px) {
  .contacts__form {
    display: flex;
    padding: 50px 15px 80px 15px;
  }
}

.copyright {
  margin-bottom: 10px;
  color: #ffffff;
}

.footer {
  background-color: #505050;
}

.footer__bottom {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
}
@media (min-width: 768px) {
  .footer__bottom {
    text-align: left;
    flex-direction: row;
    justify-content: space-between;
  }
}

.header {
  padding: 15px 0;
}
@media (min-width: 1024px) {
  .header {
    padding-bottom: 20px;
  }
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 576px) {
  .header__container {
    flex-direction: row;
  }
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
@media (min-width: 576px) {
  .header__wrapper {
    width: auto;
  }
}

.header__logo-link {
  transition: fill ease-in 0.3s;
}
.header__logo-link:hover, .header__logo-link:focus {
  fill: #fdc435;
}

.header__logo {
  width: 40px;
  height: 40px;
}
@media (min-width: 576px) {
  .header__logo {
    width: 60px;
    height: 60px;
  }
}

.header__nav {
  display: none;
}
@media (min-width: 576px) {
  .header__nav {
    display: block;
  }
}
.header__nav.is-opened {
  display: block;
}

.header__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  text-align: center;
}
@media (min-width: 576px) {
  .header__list {
    flex-direction: row;
  }
}

.header__item {
  margin-bottom: 10px;
}
.header__item:last-child {
  margin-bottom: 0;
}
@media (min-width: 576px) {
  .header__item {
    margin-right: 40px;
    margin-bottom: 0;
  }
  .header__item:last-child {
    margin-right: 0;
  }
}

.header__link {
  position: relative;
  color: #25282b;
  text-decoration: none;
}
.header__link::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #fdc435;
  visibility: hidden;
  transform: scaleX(0);
  transition: all 0.3s ease-in-out 0s;
}
.header__link:hover::before, .header__link:focus::before {
  visibility: visible;
  transform: scaleX(1);
}

.header__toggle {
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  display: block;
  width: 20px;
  height: 20px;
  margin-left: auto;
  background: url("../img/icons/icon-menu.svg") center no-repeat transparent;
  background-size: contain;
}
.header__toggle.is-opened {
  background-image: url("../img/icons/icon-croix.svg");
}
@media (min-width: 576px) {
  .header__toggle {
    display: none;
    width: auto;
    height: auto;
  }
}

.intro {
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .intro {
    margin-bottom: 60px;
  }
}

.leading__wrapper {
  display: flex;
}

.leading__info {
  width: 100%;
  text-align: center;
}

.leading__profession {
  color: #ebae14;
  text-transform: uppercase;
  font-size: 16px;
  margin-bottom: 10px;
}
@media (min-width: 1024px) {
  .leading__profession {
    font-size: 18px;
    margin-bottom: 15px;
  }
}

.leading__intro {
  max-width: 100%;
  margin: 0 0 10px 0;
  font-size: 16px;
  line-height: 24px;
}
@media (min-width: 1024px) {
  .leading__intro {
    font-size: 20px;
    line-height: 30px;
  }
}

.leading__photo {
  position: absolute;
  top: 0;
  right: 0;
  z-index: -10;
  display: none;
}
@media (min-width: 1024px) {
  .leading__photo {
    display: block;
  }
}

@media (min-width: 1024px) {
  .portfolio {
    padding-top: 50px;
  }
}
.portfolio h2 {
  margin-bottom: 16px;
}
@media (min-width: 768px) {
  .portfolio h2 {
    margin-bottom: 25px;
  }
}

.portfolio__list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}
@media (min-width: 768px) {
  .portfolio__list {
    margin-left: -13px;
    margin-right: -13px;
    flex-direction: row;
  }
}

@media (min-width: 768px) {
  .skills .title--second {
    width: 170px;
    margin-right: 30px;
  }
}
@media (min-width: 1024px) {
  .skills .title--second {
    margin-right: 180px;
  }
}

.skills__col {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 15px;
}
@media (min-width: 768px) {
  .skills__col {
    flex: 1;
    flex-direction: row;
  }
}

.skills__date {
  font-weight: bold;
  color: #25282b;
}

.skills__description {
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .skills__description {
    margin-bottom: 40px;
  }
}

.skills__link {
  text-decoration: none;
  transition: all 0.3s;
}
.skills__link:hover, .skills__link:focus {
  color: #fdc435;
}

.socials {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
@media (min-width: 768px) {
  .socials {
    align-items: center;
  }
}

.socials__item {
  margin-right: 15px;
}
.socials__item:last-child {
  margin-right: 0;
}

.socials__link {
  display: inline-block;
}
.socials__link:hover .socials__icon, .socials__link:focus .socials__icon {
  fill: #fdc435;
}

.socials__icon {
  transition: fill ease-in 0.3s;
  width: 40px;
  height: 40px;
  fill: #ffffff;
}

.work {
  position: relative;
  width: 100%;
  margin-bottom: 25px;
  border-radius: 10px;
  box-shadow: 0px 0px 12px -6px #25282b;
}
@media (min-width: 768px) {
  .work {
    width: calc(50% - 26px);
    margin-right: 13px;
    margin-left: 13px;
  }
}
@media (min-width: 1024px) {
  .work {
    width: calc(33.3333% - 26px);
    margin-bottom: 50px;
  }
}
.work picture {
  width: 100%;
}
.work h4 {
  margin: 0px;
  font-size: 18px;
  font-weight: normal;
}
.work p {
  margin: 0 0 20px 0;
}

.work__link {
  text-decoration: none;
}
@media (min-width: 768px) {
  .work__link {
    display: flex;
  }
  .work__link:hover picture, .work__link:focus picture {
    filter: blur(2px);
  }
  .work__link:hover .work__container, .work__link:focus .work__container {
    opacity: 1;
  }
}

.work__img {
  display: block;
  width: 100%;
  height: auto;
  border-radius: 10px 10px 0px 0px;
}
@media (min-width: 768px) {
  .work__img {
    opacity: 1;
    transition: 0.5s ease;
    backface-visibility: hidden;
    border-radius: 10px;
  }
}

@media (min-width: 768px) {
  .work__container {
    transition: 0.5s ease;
    opacity: 0;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    text-align: center;
  }
}

.work__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 16px 32px;
  border-radius: 0px 0px 10px 10px;
  color: #25282b;
  font-size: 16px;
}
@media (min-width: 768px) {
  .work__content {
    border-radius: 10px;
    color: #ffffff;
    background-color: rgba(37, 40, 43, 0.8);
  }
}

.work__title {
  margin: 0 0 7px 0;
  font-size: 22px;
  color: #fdc435;
}
@media (min-width: 1024px) {
  .work__title {
    font-size: 26px;
  }
}